#app {
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	position: fixed;
	top: 0;
	left: 0;
	background-color: #aeaeb6;
}

canvas {
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently */
}

.slides {
	box-sizing: border-box;
	position: absolute;
	top: 50%;
	padding: 0 10vw;
	width: 100%;
	max-width: 100%;
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently */
}

.slide {
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	height: 33vh;
	color: #fff;
	transform: translateY(-50%);
	transition: transform 1300ms cubic-bezier(0.2,1,0.3,1), opacity 500ms cubic-bezier(0.2,1,0.3,1);
}

.slide-meta {
	margin: 0;
	font-size: 1.35rem;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.slide-meta,
.slide-more {
	opacity: 0;
	transition: opacity 900ms cubic-bezier(0.2,1,0.3,1);
}

.show-meta .slide-meta,
.show-meta .slide-more {
	opacity: 1;
	transition: opacity 1500ms 300ms cubic-bezier(0.2,1,0.3,1);
	background: #000000cf;
    padding: 10px;
    border-radius: 20px;
}

.slide-more {
	border-bottom: 1px solid;
}

.slide-more:hover {
	border-bottom: 1px solid transparent;
}

.slide-title {
	font-size: 10vw;
	margin: 0;
	color: #fafafa;
	text-indent: -0.5vw;
	font-family: gibbs, sans-serif;
	font-weight: 900;
	transition: color 500ms cubic-bezier(0.2,1,0.3,1);
	text-shadow: 5px 5px #00000091;
	-webkit-text-stroke-color: #3e4b7d;
	-webkit-text-stroke-width: 3px;
	-webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.prev, 
.next {
	opacity: 0;
}

.prev {
	transform: translateY(-85%);
}

.next {
	transform: translateY(-15%);
}

.prev .slide-title,
.next .slide-title {
	-webkit-text-stroke: 1.5px #fafafa;
	text-stroke: 1.5px #fafafa;
	-webkit-text-fill-color: transparent;
	text-fill-color: transparent;
	color: transparent;
}

.scrolling > .slide {
	transform: translateY(-50%);
	opacity: 1;
}